'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Home, Search, Sparkles, Image, ShoppingCart, Settings, HelpCircle, MapPin } from 'lucide-react';
import { useCartStore } from '@/store/useCartStore';

export default function Sidebar() {
  const pathname = usePathname();
  const itemCount = useCartStore((state) => state.getItemCount());

  const navItems = [
    { href: '/', icon: Home, label: 'Home', description: 'Browse ad spaces' },
    { href: '/search', icon: Search, label: 'Search', description: 'Find locations' },
    { href: '/ai-planner', icon: Sparkles, label: 'AI Planner', description: 'Plan campaigns' },
    { href: '/design', icon: Image, label: 'Design', description: 'Coming soon' },
    { href: '/cart', icon: ShoppingCart, label: 'Cart', description: `${itemCount} items` },
  ];

  const bottomItems = [
    { href: '/settings', icon: Settings, label: 'Settings' },
    { href: '/help', icon: HelpCircle, label: 'Help' },
  ];

  return (
    <div className="sidebar">
      {/* Logo */}
      <div className="px-6 py-5 border-b border-gray-100">
        <Link href="/" className="flex items-center gap-2">
          <div className="w-10 h-10 bg-gradient-to-br from-[#E91E63] to-[#F50057] rounded-xl flex items-center justify-center">
            <MapPin className="w-6 h-6 text-white" />
          </div>
          <div>
            <h1 className="text-xl font-bold text-gray-900">Elfsod</h1>
            <p className="text-xs text-gray-500">Ad Space Marketplace</p>
          </div>
        </Link>
      </div>

      {/* Navigation */}
      <nav className="px-3 py-4">
        <div className="space-y-1">
          {navItems.map((item) => {
            const Icon = item.icon;
            const isActive = pathname === item.href || 
              (item.href !== '/' && pathname?.startsWith(item.href));
            
            return (
              <Link
                key={item.href}
                href={item.href}
                className={`flex items-center gap-3 px-3 py-2.5 rounded-lg transition-all ${
                  isActive 
                    ? 'bg-[#E91E63]/10 text-[#E91E63]' 
                    : 'text-gray-700 hover:bg-gray-50'
                }`}
              >
                <div className="relative">
                  <Icon className={`w-5 h-5 ${isActive ? 'stroke-[2.5]' : ''}`} />
                  {item.href === '/cart' && itemCount > 0 && (
                    <span className="absolute -top-1.5 -right-1.5 bg-[#E91E63] text-white text-[9px] font-bold rounded-full w-4 h-4 flex items-center justify-center">
                      {itemCount > 9 ? '9+' : itemCount}
                    </span>
                  )}
                </div>
                <div className="flex-1">
                  <div className={`text-sm font-medium ${isActive ? 'font-semibold' : ''}`}>
                    {item.label}
                  </div>
                  <div className="text-xs text-gray-500">{item.description}</div>
                </div>
              </Link>
            );
          })}
        </div>
      </nav>

      {/* Bottom items */}
      <div className="absolute bottom-0 left-0 right-0 border-t border-gray-100 bg-white">
        <div className="px-3 py-4 space-y-1">
          {bottomItems.map((item) => {
            const Icon = item.icon;
            return (
              <Link
                key={item.href}
                href={item.href}
                className="flex items-center gap-3 px-3 py-2 rounded-lg text-gray-600 hover:bg-gray-50 transition-colors"
              >
                <Icon className="w-5 h-5" />
                <span className="text-sm font-medium">{item.label}</span>
              </Link>
            );
          })}
        </div>
      </div>
    </div>
  );
}

