'use client';

import { useState } from 'react';
import { Bell, User } from 'lucide-react';
import { useLocationStore } from '@/store/useLocationStore';
import { useCampaignDatesStore } from '@/store/useCampaignDatesStore';
import LocationDateDropdown from '@/components/common/LocationDateDropdown';

export default function TopBar() {
  const [showDropdown, setShowDropdown] = useState(false);
  const [activeSearchField, setActiveSearchField] = useState<'where' | 'when' | null>(null);
  const { selectedCity } = useLocationStore();
  const { startDate, endDate } = useCampaignDatesStore();

  const formatDate = (dateString: string) => {
    if (!dateString) return '';
    const date = new Date(dateString);
    const day = date.getDate();
    const month = date.toLocaleDateString('en-US', { month: 'short' });
    return `${day} ${month}`;
  };

  return (
    <>
      <div className="bg-white border-b border-gray-200 sticky top-0 z-30">
        <div className="container-app px-6 py-4">
          <div className="flex items-center justify-between">
            {/* Search Bar */}
            <div className="flex-1 flex justify-center">
              <div className="relative inline-block">
                <div className="flex items-center bg-white border-2 border-gray-200 rounded-full shadow-sm hover:shadow-md transition-shadow">
                  {/* Where Field */}
                  <button
                    onClick={() => {
                      setActiveSearchField('where');
                      setShowDropdown(true);
                    }}
                    data-dropdown-trigger
                    className={`flex flex-col items-start px-6 py-3 rounded-l-full transition-all min-w-[200px] ${
                      activeSearchField === 'where'
                        ? 'bg-gray-50'
                        : 'hover:bg-gray-50'
                    }`}
                  >
                    <label className="text-xs font-semibold text-gray-900">Where</label>
                    <span className="text-sm text-gray-600 font-medium">
                      {selectedCity || 'Select location'}
                    </span>
                  </button>

                  {/* Divider */}
                  <div className="w-px h-10 bg-gray-200" />

                  {/* When Field */}
                  <button
                    onClick={() => {
                      setActiveSearchField('when');
                      setShowDropdown(true);
                    }}
                    data-dropdown-trigger
                    className={`flex flex-col items-start px-6 py-3 transition-all min-w-[240px] ${
                      activeSearchField === 'when'
                        ? 'bg-gray-50'
                        : 'hover:bg-gray-50'
                    }`}
                  >
                    <label className="text-xs font-semibold text-gray-900">When</label>
                    {startDate && endDate ? (
                      <span className="text-sm text-gray-600 font-medium">
                        {formatDate(startDate)} - {formatDate(endDate)}
                      </span>
                    ) : (
                      <span className="text-sm text-gray-400">Add dates</span>
                    )}
                  </button>

                  {/* Search Button */}
                  <button
                    onClick={() => {
                      setShowDropdown(false);
                      window.location.href = '/search';
                    }}
                    className="ml-2 mr-2 p-4 bg-[#E91E63] text-white rounded-full hover:bg-[#F50057] transition-all active:scale-95 shadow-md hover:shadow-lg"
                  >
                    <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                    </svg>
                  </button>
                </div>

                {/* Dropdown */}
                <LocationDateDropdown
                  isOpen={showDropdown}
                  onClose={() => {
                    setShowDropdown(false);
                    setActiveSearchField(null);
                  }}
                  mode={activeSearchField === 'where' ? 'location' : 'dates'}
                />
              </div>
            </div>

            {/* Right side actions */}
            <div className="flex items-center gap-3">
              <button className="p-2 hover:bg-gray-100 rounded-lg relative">
                <Bell className="w-5 h-5 text-gray-700" />
                <span className="absolute top-1.5 right-1.5 w-2 h-2 bg-[#E91E63] rounded-full" />
              </button>
              <button className="flex items-center gap-2 px-3 py-2 hover:bg-gray-100 rounded-lg">
                <div className="w-8 h-8 bg-gradient-to-br from-[#E91E63] to-[#F50057] rounded-full flex items-center justify-center">
                  <User className="w-5 h-5 text-white" />
                </div>
                <span className="text-sm font-medium text-gray-700 hidden md:block">Account</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

